import {
	GET_PHOTOS_SUCCESS,
	GET_PHOTOS_FAIL
} from '../constants/Page';

const VK = window.VK;
if (!VK)
	throw new Error('ошибка загрузки вк апи');


export function makeYearPhotos(photos, selectedYear) {
	let createdYear;
	let yearPhotos = [];

	photos.forEach(photo => {
		createdYear = new Date(photo.data * 1000).getFullYear();
		if (createdYear === selectedYear)
			yearPhotos.push(photo);
	});

	yearPhotos.sort((a, b) => b.likes.count - a.likes.count);

	return yearPhotos;
}


export function getMorePhotos(photosArr = [], cached, offset, count, year, dispatch) {
	const user_ids = VK.Auth.getSession().mid;
	VK.Api.call('photos.getAll', {
		extended: true, v: 5.73, user_ids, count, offset
	}, r => {
		try {
			if (offset <= r.response.count - count) {
				offset += 200;
				photosArr = photosArr.concat(r.response.items);
				getMorePhotos(photosArr, cached, offset, count, dispatch);
				console.log(1);
			} else {
				let photos = makeYearPhotos(photosArr, year);
				cached = true;
				console.log(2);
				console.log(dispatch);
				dispatch({
					type: GET_PHOTOS_SUCCESS,
					payload: photos
				});
			}
			console.log(photosArr);
		} catch(e) {
			dispatch({
				type: GET_PHOTOS_FAIL,
				error: true,
				payload: new Error(e)
			});
		}
	});
}



